import { Component, OnInit, ViewChild } from '@angular/core';
import { Router } from '@angular/router';


import { EventEmitterService } from 'app/shared/event/event-emitter.services';
import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { UnidadeMedidaDTO } from './../../../../../models/unidade-medida.dto';
import { UnidadeMedidaService } from './../unidade-medida.service';


@Component({
  templateUrl: './listar-unidade-medida.component.html',
  styleUrls: ['./listar-unidade-medida.component.scss']
})
export class ListarUnidadeMedidaComponent extends AbstractBaseComponent implements OnInit {

  @ViewChild('ExcluirUnidadeModal') popupExclusao;
  @ViewChild('ChangeSatusUnidadeModal') popupChangeStatus;
  
  
  
  unidade: UnidadeMedidaDTO;
  items: UnidadeMedidaDTO[];

  constructor(public unidadeService: UnidadeMedidaService, public router: Router) {
    super()
  }

  ngOnInit() {
    this.carregarUnidades();
  }

  selecionarItem(item: UnidadeMedidaDTO) {
    this.unidade = item;
    this.popupExclusao.show();
  }

  selecionarItemChangeStatus(item: UnidadeMedidaDTO) {
    this.unidade = item;
    this.popupChangeStatus.show();
  }

  changeStatus() {
    this.unidadeService.changeStatus(this.unidade)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Unidade Medida', 'O status da unidade de medida foi alterado com sucesso!!!')
        this.carregarUnidades();
      }, error => { });
  }

  excluirUnidade() {
    this.unidadeService.excluir(this.unidade)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Unidade Medida', 'A unidade de medida foi excluída com sucesso!!!')
        this.carregarUnidades();
      }, error => { });
  }

  salvarUnidade(value: any) {
    if (value.emAlteracao){
      this.unidadeService.atualizar(value)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Unidade Medida', 'A unidade de medida foi cadastrada com !!!')
        this.carregarUnidades();
      }, error => { });
    }else{
      this.unidadeService.salvar(value)
      .subscribe(res => {
        EventEmitterService.emitirMensagemToasterSucesso('Unidade Medida', 'A unidade de medida foi alterada com sucesso!!!')
        this.carregarUnidades();
      }, error => { });
    }
  }

  alterarUnidade(unidade: UnidadeMedidaDTO) {
    this.router.navigate([`unidadesmedidas/editar/${unidade.id}`])
  }

  private carregarUnidades() {
    this.unidadeService.findAll().subscribe(response => {
      this.items = response
    }, error => { });
  }
}



