import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Component, Output, ViewChild, OnInit, EventEmitter } from '@angular/core';

import { AbstractBaseComponent } from 'config/core/abstract-base.component';
import { UnidadeMedidaService } from './../unidade-medida.service';
import { UnidadeMedidaDTO } from './../../../../../models/unidade-medida.dto';


@Component({
  selector: 'unidade-popup-modal',
  templateUrl: './unidade-medida-popup-modal.component.html',

})
export class UnidadeMedidaPopupModalComponent extends AbstractBaseComponent implements OnInit {

  titulo: string = "Confirmar";
  unidade: UnidadeMedidaDTO;
  formUnidade: FormGroup;

  @ViewChild('modalConfirm') confirma;

  @Output() onClickConfirm = new EventEmitter<any>();

  constructor(
    public fb: FormBuilder,
    public unidadeService: UnidadeMedidaService) {
    super();
  }

  ngOnInit() {
    this.criarFormUnidade();
  }

  show() {
    this.titulo = "Cadastra unidade de medida"
    this.criarFormUnidade();
    this.confirma.show();
  }

  showAlteracao(item: UnidadeMedidaDTO) {
    this.titulo = "Alterar unidade de medida"
    this.alterarProdutoComp(item)
    this.confirma.show();
  }

  btnConfirmar(unidade: UnidadeMedidaDTO) {
    this.onClickConfirm.emit(unidade);
    this.confirma.hide();
  }

  criarFormUnidade() {
    this.formUnidade = this.fb.group({
      id: [''],
      nome: ['', Validators.required],
      sigla: ['', Validators.required],
      emAlteracao: [false]
    })
  }

  alterarProdutoComp(item: UnidadeMedidaDTO) {
    this.formUnidade = this.fb.group({
      id: [item.id],
      nome: [item.nome, Validators.required],
      sigla: [item.sigla, Validators.required],
      emAlteracao: [true]
    })
  }

}



